export default class Prepare {
	/**
	 *  qr 
	 * @param str
	 * @returns {{dt: *, fn: *, i: *, sum: *, fp: *}}
	 */
	static qr (str){
		try {
			let dt, sum, fn, i, fp;

			if (str) {
				[, dt, sum, fn, i, fp] = str.match(/t=(\w{8}T\w+)&s=(\w+\.*\w+)&fn=(\w+)&i=(\w+)&fp=(\w+)/);
			}

			return {
				dt,
				sum: sum.replace(/[.]/g, ''),
				fn,
				i,
				fp
			}
		} catch (e) {
			console.log(e);
			return {
				error: {
					message: '  QR '
				}
			}
		}
	}

	/**
	 *  
	 * @param str
	 * @returns {string}
	 */
	static date(str) {
		const dt = new Date(str);
		let monthName;
		switch (dt.getMonth() + 1) {
			case 1:
				monthName = '';
				break;
			case 2:
				monthName = '';
				break;
			case 3:
				monthName = '';
				break;
			case 4:
				monthName = '';
				break;
			case 5:
				monthName = '';
				break;
			case 6:
				monthName = '';
				break;
			case 7:
				monthName = '';
				break;
			case 8:
				monthName = '';
				break;
			case 9:
				monthName = '';
				break;
			case 10:
				monthName = '';
				break;
			case 11:
				monthName = '';
				break;
			case 12:
				monthName = '';
				break;
			default:
				break;
		}
		return `${dt.getDate()} ${monthName} ${dt.getFullYear()}`;
	}

	/**
	 *  
	 * @param n
	 * @returns {string}
	 */
	static sum(n) {
		return new Intl.NumberFormat('ru-RU', {style: 'currency', currency: 'RUB'}).format(n / 100);
	}

	/**
	 *   
	 * @param arr
	 * @returns {number}
	 */
	static totalReceiptSum(arr) {
		if (arr.length === 0) return 0;
		const reducer = (previousValue, currentValue) => previousValue + currentValue;
		return arr.map((item) => {
			return item.sum || item.totalSum;
		}).reduce(reducer);
	}

	static totalSumWithParams(arr) {
		if (arr.length === 0) return 0;
		const reducer = (previousValue, currentValue) => previousValue + currentValue;
		return arr.map((item) => {
			const {income, sum} = item;
			if (income) return sum;
			return -1*sum;
		}).reduce(reducer);
	}

	/**
	 *    / 
	 * @param arr
	 * @param income
	 * @returns {number}
	 */
	static totalSum(arr, income) {
		if (arr.length === 0) return 0;
		const reducer = (previousValue, currentValue) => previousValue + currentValue;
		const ts = arr.map(items => {
			return items.items.map(item => {
				if (income) {
					return item.income ? item.sum : 0
				}
				return !item.income ? item.sum : 0
			}).reduce(reducer)
		}).reduce(reducer);
		return income ? ts : -1 * ts;
	}
}